<template>
    <div class="search">
        <!-- 头部-->
        <header class="clearfix">
            <div class="back fl">
               <van-icon name="arrow-left" color="#666" size="30" @click="back()"/>
            </div>
            <div class="searchbox fl">
                <form action="/">
                  <van-search
                    v-model="value"
                    placeholder="请输入搜索影片的标题"
                    @input="showsearchMovie()"
                  />
                </form>
            </div>
        </header>
  
        <!-- 影片基本信息 -->
        <div class="searchlist">
            <div class="searchitem1 clearfix" >
                <div class="search_img fl">
                   <img class="autoimg" src="http://img5.mtime.cn/mg/2019/08/28/110121.80393257_270X405X4.jpg" alt="">
                </div>
                <div class="search_msg fl">
                  <div class="mi-title one-txt">宠爱</div>
                  <div>导演：杨子</div>
                  <div class="type_language">
                    <span class="fl">演员: </span>
                    <span class="actors one-txt fl">于和伟/吴磊/张子枫/钟汉良</span>
                  </div>
                  <div class="mi_grade">剧情/家庭/爱情</div>
                </div>
            </div>
            
            <div class="searchitem clearfix" >
                <div class="search_img fl">
                   <img class="autoimg" :src="searchMovieData.poster" alt="">
                </div>
                <div class="search_msg fl">
                  <div class="mi-title one-txt">{{searchMovieData.title}}</div>
                  <div>导演：{{searchMovieData.directors}}</div>
                  <div class="type_language">
                    <span class="fl">演员: </span>
                    <span class="actors one-txt fl">{{searchMovieData.actors}}</span>
                  </div>
                  <div class="mi_grade">{{searchMovieData.genres}}</div>
                </div>
            </div>
        </div>
    </div>

    <!-- 
        验证登录 _ 开启加载提示 _关闭加载提示 _获取所有用户收藏的电影数据 _ 获取当前用户收藏的电影数据
        _ 生成页面数据 _遍历数据_  back(){} _viewMovieDetail(o){} _ delsearchMovie(id){}-->

</template>

<script>
    import $ from 'jquery'
    import {createNamespacedHelpers} from 'vuex'
    const {mapState} = createNamespacedHelpers('searchMovieModule');
    export default {
        name:'searchMovie',
        
        data() {
          return {
            value: '',
          };
        },

        computed:{
            ...mapState(['searchMovieData'])
        },

        methods:{
            // 返回上一步
            back() {
                this.$router.go(-1);
            },

            // 触发焦点
            showsearchMovie(){

                // console.log('this.value=>',this.value);

                if(this.value == '' || undefined){
                    return;
                }else{
                    $('.searchitem').show();
                    this.getsearchMovie();
                }
            },

            getsearchMovie(){
                let self = this;

                let value = [];
                value.push(this.value);

                $.ajax({
                    type:'GET',
                    url:'http://v.juhe.cn/movie/index',
                    data:{
                      key:'013ef86b9f45bfcb925dac0635bcc8ba',
                      title:value[0]
                    },
                    dataType: 'jsonp', //跨域请求解决方法之一jsonp类型
                    success:function(data){
                        // console.log('data=>',data);
                        self.$store.commit('searchMovieModule/getsearchMovieData', data.result[0])
                        
                    }
                })

            }

        },
    }
</script>

<style lang="less" scoped>
.search{
    
    /deep/.van-search__content {
        background-color: transparent;
        border: 1px solid #333;
    }
    header{
        height: 54px;
        padding-top: 10px;
        >.back{
            // width: 10%;
            color: #333;
            font-size: 14px;
            margin: 12px auto;
        }
        .searchbox{
            width: 90%;
        }

    }

    .searchlist{
        padding: 0 20px;
        margin-top: 10px;
        overflow-y: auto;

        // 测试
        .searchitem1{
            padding:4px 10px;
            background-image: linear-gradient(to top, #fff,rgba(255, 255, 255, 0.7),rgba(255, 255, 255, 0.9));
            border: 1px solid #697A9C;
            box-shadow: 4px 4px 3px #ccc;
            border-radius: 2px;
            position: relative;
            margin-bottom: 10px; 
        }

        .searchitem{
            padding:4px 10px;
            background-image: linear-gradient(to top, #fff,rgba(255, 255, 255, 0.7),rgba(255, 255, 255, 0.9));
            border: 1px solid #697A9C;
            box-shadow: 4px 4px 3px #ccc;
            border-radius: 2px;
            position: relative;
            margin-bottom: 10px;
            display: none;
        }
        .search_img{
            width: 100px;
            // height: 120px;
            overflow: hidden;
            background-color:#f5f5f5;
            >img{
            overflow: hidden;
            border-radius: 5px;
            }
        }
        .search_msg{
            width: calc(~"100% - 115px");
            margin-left: 15px;
            font-size: 14px;
            margin-top: 14px;
            >div{
              line-height: 30px;
            }
            .mi-title{
              // width: calc(~"100% - 120px");
              font-size: 20px;
              color: #000;
              font-weight: 600;
            }
            .type_language{
                .actors{
                    width: 80%;
                    overflow: hidden;
                }
            }
            .mi_grade{
              font-size: 16px;
              font-weight: 700;
              color: rgb(255, 1, 141);
            }
        }

    }
}
</style>